import { Helmet } from "react-helmet";
import React from "react";

import { Banner } from "../../components/banner";
import { ScrollToTop } from "../../common/scroll_to_top_init";
import { LazyLoadingImage } from "../../common/image_lazy_loading";
import { AOS_ANIMATION } from "../../common/aos_animation";

import styles from "./styles.module.css";

import bannerImg from "../../images/portfolio/banner_img.jpg";

import themedEvents_img_01 from "../../images/portfolio/themed_events/themedEvents_img_01.jpg";
import themedEvents_img_02 from "../../images/portfolio/themed_events/themedEvents_img_02.jpg";

import stageSetup_img_01 from "../../images/portfolio/stage_setup/stageSetup_img_01.jpg";
import stageSetup_img_02 from "../../images/portfolio/stage_setup/stageSetup_img_02.jpg";

import photoBooth_img_01 from "../../images/portfolio/photo_booth/photoBooth_img_01.jpg";
import photoBooth_img_02 from "../../images/portfolio/photo_booth/photoBooth_img_02.jpg";

import annualMeet_img_01 from "../../images/portfolio/annual_meet/annualMeet_img_01.jpg";

import influencer_marketing_img_01 from "../../images/portfolio/influencer_marketing/influencerMarketing_img_01.jpg";

import productLaunchEvent_img_01 from "../../images/portfolio/product_launch_event/productLaunchEvent_img_01.jpg";

const images = {
  themedEvents: [themedEvents_img_01, themedEvents_img_02],
  stageSetup: [stageSetup_img_01, stageSetup_img_02],
  photoBooth: [photoBooth_img_01, photoBooth_img_02],
  annualMeet: [annualMeet_img_01],
  influencerMarketing: [influencer_marketing_img_01],
  productLaunchEvent: [productLaunchEvent_img_01],
};

export default function Portfolio() {
  ScrollToTop();

  return (
    <React.Fragment>
      <Helmet>
        <title>
          Portfolio | Woodhead Events - Showcasing Our Best Events in Chennai
        </title>
        <meta
          name="description"
          content="Explore the portfolio of Woodhead Events and see how we have successfully curated and managed a variety of bespoke events, reflecting our commitment to excellence and unique event solutions."
        />
        <meta
          name="keywords"
          content="Event Portfolio, Event Gallery, Our Work, Woodhead Events Portfolio, Showcase of Our Event Management Projects, Examples of Successful Events by Woodhead Events"
        />
      </Helmet>
      <Banner image={bannerImg} name="Portfolio" />
      <div className={styles.portfolioWrapper}>
        <Section title="Themed Events" images={images.themedEvents} />
        <Section title="Stage setup" images={images.stageSetup} />
        <Section title="Photo booth" images={images.photoBooth} />
        <Section title="Annual Meet" images={images.annualMeet} single />
        <Section
          title="Influencer Marketing"
          images={images.influencerMarketing}
        />
        <Section
          title="Product Launch Event"
          images={images.productLaunchEvent}
        />
      </div>
    </React.Fragment>
  );
}

const Section = ({ title, images }) => {
  const containerClass =
    images.length === 1
      ? `${styles.imgContainer} ${styles.singleImgContainer}`
      : styles.imgContainer;

  return (
    <div className={styles.sectionContainer}>
      <h2
        className={`section-title ${styles.sectionTitle}`}
        data-aos={AOS_ANIMATION.FADE_UP.TYPE}
        data-aos-duration={AOS_ANIMATION.FADE_UP.DURATION}
      >
        {title}
      </h2>
      <div className={containerClass}>
        {images.map((src, index) => (
          <LazyLoadingImage
            key={index}
            src={src}
            data-aos={AOS_ANIMATION.FADE_UP.TYPE}
            data-aos-duration={AOS_ANIMATION.FADE_UP.DURATION}
          />
        ))}
      </div>
    </div>
  );
};
